<script>
	export default {
		props: ['id'],
		data() {
			return {
				comment: ''
			}
		},
		computed: {
			post() {

				const id = this.id;
				return this.$store.getters.onePostbyId(id);
			},
			getComments() {
				return this.$store.getters.getComments(this.id)
			}

		},
		methods: {
			addComment(id) {
				console.log(this.comment)
				this.$store.dispatch('addComment', {comment: this.comment, id: this.id})
			}
		}
	}
</script>

